import * as React from "react"

import { outer } from "./segment.module.scss"

const Segment = ({ title, subtitle, children }) => {
    return (
        <div className={ outer }>
            <section>
                <h2>{ title }</h2>
                <p>{ subtitle }</p>
                <div>{ children }</div>
            </section>
        </div>
    )
}

export default Segment